@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@400;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap);
/* Do Hyeon */
/* Nanum Myeongjo Regular/Extra-bold */
/* Black Han Sans */
:root {
	--dark-brown: #4f311a;
	--light-brown: #e4d7c2;
}

.container {
	min-width: 10rem;
	padding: 10rem 0 0 0;

	display: flex;
	flex-direction: column;
	align-items: center;
}

/* input 에 화살표 지우기 */
.container input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	        appearance: none; 
}

.container option, input, select, button {
	padding: 0;
	margin: 0;
	border: none;
	background-color: rgba(0, 0, 0, 0);
	width: 100%;
	height: 100%;
	outline: none;
}

.body__text {
	padding: .5rem 0;
	color: #4f311a;
	color: var(--dark-brown);
	text-align: center;
	font-family: 'Nanum Myeongjo';
	font-size: 1rem;
	line-height: 1.5em;
}

.logoImage {
	max-width: 15rem;
	margin: 10rem 0rem 2rem 0rem;
}

.navButton {
	width: 14em;
	padding: .8rem 2rem;
	margin: 2rem;
	
	display: flex;
	justify-content: space-around;

	background-color: white;
	border: .4rem solid #4f311a;
	border: .4rem solid var(--dark-brown);
	box-shadow: 8px 8px 0px 0 #4f311a;
	box-shadow: 8px 8px 0px 0 var(--dark-brown);
}

.navButton__link {
	color: #4f311a;
	color: var(--dark-brown);
	font-size: 1.2em;
	font-family: "돋움", "Do Hyeon";
	text-align: center;
	text-decoration: none;
}

.container {
	min-width: 10rem;
	padding: 3rem 0 0 0;

	display: flex;
	flex-direction: column;
	align-items: center;
}

.logoImage_small {
	max-width: 100px;
}

.title {
	padding: .5rem 0 2rem 0;
	color: var(--dark-brown);
	text-align: center;
	font-family: 'Black Han Sans';
	font-size: 2rem;
}


.numberBox {
	/* display: flex; */
	border: .2rem solid white;
	/* background-color: var(--dark-brown); */
	background-color: rgba(0, 0, 0, 0);
	display: flex;
	align-items: center;
}

.numberBox input {
	width: 5rem;
	padding: 1rem .5rem;
	border: none;
	background-color: rgba(0, 0, 0, 0);;
	
	text-align: center;
	color: var(--dark-brown);
}

.numberBox button {
	width: 4rem;
	padding: 1rem;
	border: none;
	color: white;
	background-color: var(--dark-brown);
}

.errorMsg {
	height: 1rem;
	padding: .5rem 0 0 0;
	color: brown;
	text-align: center;
	font-family: 'Nanum Myeongjo';
	font-size: .8em;
}

.people {
	display: flex;
	flex-direction: column;
	align-items: center;
	/* background-color: red; */
}

.table__row {
	display: flex;
	flex-direction: row;
	align-items: flex-end;
}

.table__id,
.table__name,
.table__idAndName,
.table__pay,
.table__checkbox,
.table__delButton {
	font-size: .8em;
	padding: .2rem;
	margin: 0;
	border: .1rem solid rgba(0, 0, 0, 0);
	height: 1rem;
}

.table__id {
	width: 2rem;
	text-align: center;
}

.table__name {
	width: 6rem;
	background-color: rgba(0, 0, 0, 0);
}

.table__pay {
	width: 5rem;
	justify-content: center;
	display: flex;
	flex-direction: row;
}

.table__pay input {
	background-color: rgba(0, 0, 0, 0);
	width: 5em;
	padding-right: 0.2rem;
	text-align: right;
}

.table__checkbox {
	width: 1.5rem;
}

/* .table__checkbox input + label::before{
	border: 2px solid red;
	background-color: red;
} */

.table__checkboxTag {
	text-align: right;
	-webkit-writing-mode: vertical-lr;
	    -ms-writing-mode: tb-lr;
	        writing-mode: vertical-lr;
	height: 5rem;
}

.table__delButton {
	width: 2rem;
	background-color: rgba(0, 0, 0, 0);
}

.table__delButton button {
	color: brown;
}

.whiteBox {
	border: .1rem solid white;
}

.payments {
	margin: 0;
	padding: 0;
}

.addButton {
	width: 2rem;
	height: 2rem;
	border-radius: 1rem;
	background-color: var(--dark-brown);
	margin: 1.5rem;

	color: white;
	font-size: 1.5rem;

	box-shadow:.2em .2em 0 0 white;
}
.table__name {
	text-align: center;
}

.empty {
	height: 2rem;
}
body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 
		Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
	background-color: var(--light-brown);
}
